:root {
  --header-bg: #fdfdfd;
  --body-bg: #fafafa;
  --card-bg: #fff;
  --text-main: #1a1a1a;
  --text-muted: #666;
  --accent: #000;
  --border: #eee;
  --shadow: 0 4px 20px #00000014;
  --success: #22c55e;
  --important-bg: #0ee41c94;
  --not-important-bg: #e8311194;
  --transition: all .2s cubic-bezier(.4, 0, .2, 1);
  --q-do: #dcfce7;
  --q-do-text: #166534;
  --q-schedule: #dbeafe;
  --q-schedule-text: #1e40af;
  --q-delegate: #fef9c3;
  --q-delegate-text: #854d0e;
  --q-eliminate: #fee2e2;
  --q-eliminate-text: #991b1b;
}

body.dark-mode {
  --header-bg: #1a1a1a;
  --body-bg: #121212;
  --card-bg: #1e1e1e;
  --text-main: #f5f5f5;
  --text-muted: #aaa;
  --accent: #fff;
  --border: #333;
  --shadow: 0 4px 20px #0006;
  --q-do: #064e3b;
  --q-do-text: #50c5a6;
  --q-schedule: #1e3a8a;
  --q-schedule-text: #93c5fd;
  --q-delegate: #713f12;
  --q-delegate-text: #fde047;
  --q-eliminate: #7f1d1d;
  --q-eliminate-text: #fca5a5;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--body-bg);
  color: var(--text-main);
  flex-direction: column;
  min-height: 100vh;
  font-family: Segoe UI, system-ui, -apple-system, sans-serif;
  transition: background-color .3s;
  display: flex;
}

.hidden {
  display: none !important;
}

:focus-visible {
  outline: 2px solid var(--accent);
  outline-offset: 2px;
}

header {
  background-color: var(--header-bg);
  z-index: 1000;
  border-bottom: 1px solid var(--border);
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 60px;
  display: flex;
  position: sticky;
  top: 0;
  box-shadow: 0 1px 10px #0000000d;
}

.logo {
  color: var(--text-main);
  opacity: .8;
  font-size: 1.5rem;
}

.logo span {
  background-color: var(--accent);
  color: var(--card-bg);
  width: 45px;
  height: 45px;
  transition: var(--transition);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
  padding-bottom: 6px;
  font-family: Amiri, serif;
  font-size: 1.8rem;
  display: inline-flex;
}

.header-actions {
  align-items: center;
  gap: 15px;
  display: flex;
}

.theme-toggle {
  border: 1px solid var(--border);
  cursor: pointer;
  color: var(--text-main);
  width: 38px;
  height: 38px;
  transition: var(--transition);
  background: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 8px;
  display: flex;
}

.view-switch {
  background: var(--border);
  border-radius: 10px;
  gap: 4px;
  padding: 4px;
  display: flex;
}

.view-switch button {
  cursor: pointer;
  color: var(--text-muted);
  transition: var(--transition);
  background: none;
  border: none;
  border-radius: 8px;
  padding: 8px 16px;
}

.view-switch button.active-view {
  background: var(--card-bg);
  color: var(--accent);
  box-shadow: 0 2px 5px #0000001a;
}

main {
  flex-grow: 1;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  padding: 1.5rem;
}

.add-section {
  background: var(--card-bg);
  border: 1px solid var(--border);
  border-radius: 12px;
  margin-bottom: 1.5rem;
  padding: 1.2rem;
}

form {
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  align-items: end;
  gap: 1rem;
  display: grid;
}

.input-group {
  flex-direction: column;
  gap: .3rem;
  display: flex;
}

label {
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: .5px;
  font-size: .7rem;
  font-weight: 700;
}

select, input[type="text"] {
  border: 1px solid var(--border);
  background: var(--card-bg);
  color: var(--text-main);
  border-radius: 6px;
  outline: none;
  padding: .6rem;
  font-size: .9rem;
  transition: border-color .2s;
}

input[type="text"]:focus {
  border-color: var(--accent);
}

button[type="submit"] {
  background: var(--accent);
  color: var(--body-bg);
  border: 1px solid var(--accent);
  cursor: pointer;
  transition: var(--transition);
  border-radius: 6px;
  padding: .65rem;
  font-weight: 600;
}

button[type="submit"]:hover {
  color: var(--text-main);
  background-color: #0000;
}

.filter-nav ul {
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
  margin-bottom: 1rem;
  list-style: none;
  display: flex;
}

.filter-nav button {
  border: 1px solid var(--border);
  cursor: pointer;
  color: var(--text-main);
  transition: var(--transition);
  background: none;
  border-radius: 20px;
  padding: .4rem 1rem;
  font-size: .8rem;
}

.selectedFilter {
  font-weight: bold;
  transform: scale(1.1);
  border-color: var(--accent) !important;
  background: var(--card-bg) !important;
}

.progress-container {
  background: var(--border);
  border-radius: 10px;
  width: 100%;
  height: 8px;
  margin-bottom: 1rem;
  overflow: hidden;
}

.progress-bar {
  background: var(--success);
  width: 0%;
  height: 100%;
  transition: width .6s;
}

.goals-display {
  background: var(--card-bg);
  border: 1px solid var(--border);
  box-shadow: var(--shadow);
  border-radius: 12px;
  padding: 2rem;
}

.goal-item {
  border-bottom: 1px solid var(--border);
  cursor: grab;
  justify-content: space-between;
  align-items: center;
  padding: 1.2rem 0;
  display: flex;
}

.goal-item.dragging {
  opacity: .4;
  border: 2px dashed var(--accent);
  background: var(--body-bg);
}

.goal-title {
  border: 1px solid var(--border);
  text-align: center;
  color: var(--text-main);
  border-radius: 24px;
  padding: 8px 16px;
  font-size: 1.25rem;
  font-weight: 600;
  display: inline-block;
}

.important .goal-title {
  background-color: var(--important-bg);
}

.not-important .goal-title {
  background-color: var(--not-important-bg);
}

.matrix-grid {
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  display: grid;
}

.quadrant {
  background: var(--card-bg);
  border: 1px solid var(--border);
  min-height: 220px;
  box-shadow: var(--shadow);
  border-radius: 15px;
  padding: 1.5rem;
}

.q-title {
  text-transform: uppercase;
  border-radius: 6px;
  margin-bottom: 1rem;
  padding: 6px 12px;
  font-size: .85rem;
  font-weight: 800;
  display: inline-block;
}

.q-do {
  background: var(--q-do);
  color: var(--q-do-text);
}

.q-schedule {
  background: var(--q-schedule);
  color: var(--q-schedule-text);
}

.q-delegate {
  background: var(--q-delegate);
  color: var(--q-delegate-text);
}

.q-eliminate {
  background: var(--q-eliminate);
  color: var(--q-eliminate-text);
}

.matrix-item {
  border-bottom: 1px solid var(--border);
  align-items: center;
  padding: 10px 10px 10px 15px;
  font-size: .95rem;
  display: flex;
  position: relative;
}

.matrix-item.priority-high {
  border-left: 4px solid #ef4444;
}

.matrix-item.priority-low {
  border-left: 4px solid #3b82f6;
}

.actions-bar {
  justify-content: flex-end;
  gap: 15px;
  margin-bottom: 1.5rem;
  display: flex;
}

.text-btn {
  color: var(--text-muted);
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 4px;
  padding: 4px 8px;
  font-size: .75rem;
  font-weight: 600;
}

.text-btn:hover {
  color: #ef4444;
  background: #ef44441a;
}

.goal-action {
  align-items: center;
  gap: 10px;
  display: flex;
}

input[type="checkbox"] {
  appearance: none;
  border: 2px solid var(--accent);
  cursor: pointer;
  width: 22px;
  height: 22px;
  transition: var(--transition);
  border-radius: 6px;
  position: relative;
}

input[type="checkbox"]:checked {
  background-color: var(--accent);
}

input[type="checkbox"]:checked:after {
  content: "";
  color: var(--body-bg);
  font-family: "Font Awesome 6 Free";
  font-size: 12px;
  font-weight: 900;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.delete-btn {
  color: #ff4d4d;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 1.4rem;
  transition: transform .2s;
}

.delete-btn:hover {
  transform: scale(1.2);
}

.goal-item.completed {
  opacity: .5;
}

.goal-item.completed .goal-title {
  filter: grayscale();
  text-decoration: line-through;
}

.matrix-item.completed span {
  opacity: .6;
  text-decoration: line-through;
}

.status-summary {
  text-align: right;
  color: var(--text-muted);
  margin-top: auto;
  padding: 1.5rem 0;
  font-size: .9rem;
}

footer {
  border-top: 1px solid var(--border);
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  font-size: 1rem;
  display: flex;
}

footer span {
  color: var(--card-bg);
  background-color: var(--accent);
  width: 40px;
  height: 40px;
  transition: var(--transition);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-right: 5px;
  padding-bottom: 5px;
  font-family: Amiri, serif;
  font-size: 1.4rem;
  font-weight: bold;
  display: inline-flex;
}

.empty-msg {
  color: var(--text-muted);
  margin-top: 5px;
  font-size: .8rem;
  font-style: italic;
}

@media (width <= 768px) {
  .matrix-grid, form {
    grid-template-columns: 1fr;
  }
}
